.player {
  position: relative;
  float: right;
  width: 176px;
  height: 176px;
  background: linear-gradient(0deg, #222, #111);
  border-radius: 100%;
  border: solid 2px #000;
  box-shadow: inset 0 1px 2px rgba(255, 255, 255, 0.2), 0 4px 2px rgba(0, 0, 0, 0.2);

  .fa {
    font-size: 1.2rem;
    position: absolute;
    cursor: pointer;
    transition: all 0.2s ease;

    &:hover {
      color: cyan;
      text-shadow: 0 0 4px cyan;
    }

    &.fa-play {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 88px;
      height: 88px;
      border: solid 2px #000;
      box-shadow: inset 0 1px 2px rgba(255, 255, 255, 0.2), 0 0 8px rgba(0, 0, 0, 0.6);
      border-radius: 100%;
      line-height: 88px;
      text-align: center;
      background: linear-gradient(0deg, #111, #222);
      font-size: 40px;
      box-sizing: border-box;
    }

    &.fa-repeat {
      top: 3%;
      left: 50%;
      transform: translateX(-50%);
      font-size: 28px;
    }

    &.fa-fast-backward {
      top: 50%;
      left: 4%;
      transform: translateY(-50%);
      font-size: 30px;

    }

    &.fa-fast-forward {
      top: 50%;
      left: 79%;
      transform: translateY(-50%);
      font-size: 30px;
    }

    &.fa-random {
      top: 81%;
      left: 50%;
      transform: translateX(-50%);
      font-size: 22px;

    }
  }
}

footer {
  clear: both;
  padding: 2rem 2rem;
  color: #999;
  text-shadow: 2px 2px #111;
  font-family: 'Roboto', sans-serif;
  box-sizing: border-box;

  .title {
    text-align: center;
    padding-bottom: 0.5rem;
  }

  .time-tracker {
    border-top: solid 1px #000;
    box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.1);
    padding-top: 0.5rem;
    box-sizing: border-box;

    .timer {
      position: relative;
      float: left;
      width: 90%;
      height: 2rem;
      cursor: pointer;

      .bg {
        position: relative;
        top: 50%;
        margin-right: 0.5rem;
        transform: translateY(-50%);
        height: 0.6rem;
        border-top: solid 1px #000;
        border-radius: 10px;
        background: none;
        box-shadow: inset 0 -1px 1px rgba(255, 255, 255, 0.2);
        overflow: hidden;
        box-sizing: border-box;

        .bgSeekBar {
          top: 50%;
          transform: translateY(-50%);
          position: absolute;
          height: 0.2rem;
          border-radius: 10px;
          background: cyan;
          box-shadow: 0 0 2px 2px cyan;
        }
      }
    }

    .time {
      float: left;
      width: 10%;
      line-height: 2rem;
    }
  }
}