.header {
  display: flex;
  justify-content: center;

  .img {
    position: absolute;
    bottom: 0;
    width: 325px;
  }

  .loginBlock {
    position: absolute;
    right: 0;
    top: -70px;

    .logPNG {
      width: 150px;
      cursor: pointer;
      transition: 2s;
      transform: translateX(-176%);
      position: relative;
    }

    .logPNG2 {
      width: 150px;
      transform: translateX(0);
      transition: 2s;
      position: relative;
    }
  }

  .loginBlock a {
    color: white
  }
}


